export const actions = {
    setChapterHistory({ state }, data) {
        state.chapterHistory = data
    },
    setChaptersList({ state }, data) {
        state.chaptersList = data
        // console.log(state.chaptersList)
    },
    setChapterText({ state }, data) {
        let text = data.cpContent.split("\n");
        let txtArr = [];
        text.map((v) => {
            v = v.trim();
            txtArr.push(v);
        });
        data.cpContent=txtArr
        state.chapterText = data
        // console.log(state.chapterText)
        return state.chapterText
    }
}