export const actions = {
  // 书籍阅读历史初始化
  setHistoryData({ state }, data) {
    let isAt = state.historyData.some(v=>{
      return v._id == data._id
    })
    if(!isAt){
      state.historyData.push(data)
    }
    localStorage.setItem(
      "BOOKHISTORY",
      JSON.stringify(state.historyData)
    );
  },

  // 书架书籍初始化
  setClassifyData({ state }, data) {
    // console.log("setClassifyData==>data",data)
    state.classifyData.push(data)
    localStorage.setItem(
      "CLASSIFYBOOK",
      JSON.stringify(state.classifyData)
    );
  },

  // 加入阅读历史
  addHistoryData({ state }, data) {
    // console.log("前",state.historyData,data)
    for (let i = 0; i < state.historyData.length; i++) {
      if (state.historyData[i]._id == data._id) {
        state.historyData.splice(i, 1)
        break
      }
    }
    state.historyData.unshift(data)
    // console.log("后",state.historyData,data)
    localStorage.setItem(
      "BOOKHISTORY",
      JSON.stringify(state.historyData)
    );
  },


  // 加入书架
  addClassifyData({ state }, data) {
    for (let i = 0; i < state.classifyData.length; i++) {
      if (state.classifyData[i]._id == data._id) {
        state.classifyData.splice(i, 1)
        break
      }
    }
    state.classifyData.unshift(data)
    localStorage.setItem(
      "CLASSIFYBOOK",
      JSON.stringify(state.classifyData)
    );
  },

  // 书籍阅读历史书架状态
  setHistoryDataBookState({ state }, data) {
    // console.log("找到目标，更新状态")
    let historyData = [];
    for (let i = 0; i < state.historyData.length; i++) {
      if (state.historyData[i]._id == data._id) {
        data.bookState.isAtClassify = true
        state.historyData[i] = data
      }
      historyData.push(state.historyData[i])
    }
    state.historyData = Object.assign(historyData)
    localStorage.setItem(
      "BOOKHISTORY",
      JSON.stringify(state.historyData)
    );
  },
}