export const actions = {
    // 定义书籍详情
      setBookDetail({ state }, data) {
        data.latelyFollower = String(
          data.latelyFollower / 10000
        ).slice(0, -2);
        data.totalFollower = String(data.totalFollower / 10000).slice(0, -2);
        data.wordCount = parseInt(data.wordCount / 10000);
        state.bookDetail = data
        state.bookDetail.cover = unescape(state.bookDetail.cover.slice(7))
        // console.log(state.bookDetail)
      },


      changeBookDetail({ state }, data) {
        state.bookDetail = data 
      },
  
      // 通过id定义书籍源id
      setBookOriginIdListById({ state }, id) {
        state.bookOriginId = id
        // console.log("源id==>",id)
      },
  
      // 通过id定义书籍源link
      setBookIdListById({ state }, id) {
        state.bookId = id
        // console.log("id==>",id)
      },
  
      // 通过id定义书籍目录
      setDirectoryListByLink({ state }, data) {
        state.directoryList = data
        // console.log(data)
      },

      // 设置书籍状态
      setBookDetailBookState( {state} , data){
        state.bookDetail.bookState = Object.assign(data)
        // console.log("state.bookDetail==>",state.bookDetail)
        return state.bookDetail
      }    
      

}