<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  created() {
    this.getCategoriesData();
    this.setHistoryData();
    // this.setClassifyData();
  },
  methods: {
    // 设置阅读记录
    setHistoryData() {
      // 获取本地存储
      let historyData = localStorage.getItem("BOOKHISTORY");
      // console.log("historyData==>", historyData);
      // 如果本地存储为null，添加本地存储
      if (!historyData) {
        historyData = [];
        localStorage.setItem("BOOKHISTORY", JSON.stringify(historyData));
      } else {
        // 如果存在本地存储，添加书架数据
        // console.log("historyData", this.$store.state.bookrackStore.historyData);
        if (JSON.parse(historyData).length == 0) return;
        for (let i = 0; i < JSON.parse(historyData).length; i++) {
          this.$store.dispatch(
            "bookrackStore/setHistoryData",
            JSON.parse(historyData)[i]
          );
        }
        localStorage.setItem(
          "BOOKHISTORY",
          JSON.stringify(this.$store.state.bookrackStore.historyData)
        );
      }
    },

    // 设置书架记录
    setClassifyData() {
      // 获取本地存储
      let classifyData = localStorage.getItem("CLASSIFYBOOK");
      // console.log("classifyData==>", classifyData);
      // 如果本地存储为null，添加本地存储
      if (!classifyData) {
        classifyData = [];
        localStorage.setItem("CLASSIFYBOOK", JSON.stringify(classifyData));
      } else {
        // 如果存在本地存储，添加书架数据
        if (JSON.parse(classifyData).length == 0) return;
        for (let i = 0; i < JSON.parse(classifyData).length; i++) {
          this.$store.dispatch(
            "bookrackStore/setClassifyData",
            JSON.parse(classifyData)[i]
          );
        }
        localStorage.setItem(
          "CLASSIFYBOOK",
          JSON.stringify(this.$store.state.bookrackStore.classifyData)
        );
      }
    },

    // 获取分类数据
    getCategoriesData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/rank-category",
      })
        .then((result) => {
          this.$toast.clear();
          let categoriesData = Object.assign(result);
          // console.log(categoriesData);
          this.setCategoriesData(categoriesData);
          // this.$store.dispatch("rankStore/setCategoriesData", result);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 返回promise对象数组
    getRankData(ids) {
      let axiosArr = [];
      ids.map((v) => {
        axiosArr.push(
          this.axios({
            method: "GET",
            url: "/rank/" + v,
          })
        );
      });
      return this.axios.all(axiosArr);
    },

    // 设置排行榜初始书籍列表
    setCategoriesData(categoriesData) {
      // console.log(categoriesData);
      let ids = [];
      for (let key in categoriesData.data) {
        if (key == "ok") continue;
        // console.log(key,categoriesData.data[key][0]._id)
        ids.push(categoriesData.data[key][0]._id);
      }
      // console.log(ids);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      // 防止因为网络问题请求时间过长，提前停止加载动画
      // setTimeout(() => {
      //   this.$toast.clear();
      // }, 500);
      this.getRankData(ids).then((result) => {
        this.$toast.clear();
        for (let key in categoriesData.data) {
          if (key == "ok") continue;
          // console.log(key);
          result.map((v) => {
            if (v.data.ranking.gender == key) {
              // 人气降序
              v.data.ranking.books.sort((a, b) => {
                return b.latelyFollower - a.latelyFollower;
              });
              categoriesData.data[key][0].booksData = v.data.ranking.books;
              categoriesData.data[key][0].booksData.map((v) => [
                (v.cover = unescape(v.cover.slice(7))),
              ]);
            }
          });
        }

        this.$store.dispatch("rankStore/setCategoriesData", categoriesData);
        // console.log("categoriesData==>", categoriesData);
      });
    },
  },
};
</script>


