export const routes = [
    {
        path: '/main',
        name: 'Main',
        component: () => import('../views/Main.vue'),
        children: [
            {
                path: 'home',
                name: 'Home',
                component: () => import('../views/mainViews/Home.vue')
            },
            {
                path: 'classify',
                name: 'Classify',
                component: () => import('../views/mainViews/Classify.vue')
            },
            {
                path: 'bookrack',
                name: 'Bookrack',
                component: () => import('../views/mainViews/Bookrack.vue')
            },
            {
                path: 'my',
                name: 'My',
                component: () => import('../views/mainViews/My.vue')
            },
        ]
    },
    {
        path: '/detail',
        name: 'Detail',
        component: () => import('../views/Detail.vue')
    },
    {
        path: '/directory',
        name: 'Directory',
        component: () => import('../views/Directory.vue')
    },
    {
        path: '/chapters',
        name: 'Chapters',
        component: () => import('../views/Chapters.vue')
    },
    {
        path: '/classifyList',
        name: 'ClassifyList',
        component: () => import('../views/ClassifyList.vue')
    },
    {
        path: '/history',
        name: 'History',
        component: () => import('../views/History.vue')
    },
    {
        path: '/search',
        name: 'Search',
        component: () => import('../views/Search.vue')
    },
    {
        path: '/rank',
        name: 'Rank',
        component: () => import('../views/Rank.vue')
    },
    {
        path: "*",
        redirect: "/main/home"
    }

]