import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import VueAxios from 'vue-axios'
import 'font-awesome/css/font-awesome.css'

import "lib-flexible/flexible"
import {
  Tabbar,
  TabbarItem,
  Button,
  Tab,
  Tabs,
  Toast,
  Icon,
  NavBar,
  Cell,
  CellGroup,
  List,
  Swipe,
  SwipeItem,
  Search,
  TreeSelect,
  Popup 
} from 'vant';

Vue
  .use(Tabbar)
  .use(TabbarItem)
  .use(Button)
  .use(Tab)
  .use(Tabs)
  .use(Toast)
  .use(Icon)
  .use(NavBar)
  .use(Cell)
  .use(CellGroup)
  .use(List)
  .use(Swipe)
  .use(SwipeItem)
  .use(Search)
  .use(TreeSelect)
  .use(Popup)


Vue.config.productionTip = false

// 注册axiox
Vue.use(VueAxios, axios)
// 设置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'

//设置请求基础路径
axios.defaults.baseURL = 'http://novel.kele8.cn/'

//全局注册过滤器, 一般用于格式化数据
Vue.filter('formatDate', (value, format) => {
  //value: 需要格式化的数据
  //format: 指定格式 yyyy-MM-dd hh:mm:ss

  let date = new Date(value);

  //获取年份
  let year = date.getFullYear();
  if (/(y+)/.test(format)) {
    //获取匹配组的内容
    let content = RegExp.$1;
    format = format.replace(content, year.toString().slice(4 - content.length));
  }

  let o = {
    M: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    m: date.getMinutes(),
    s: date.getSeconds()
  };
  for (let key in o) {
    //构造动态正则
    let reg = new RegExp(`(${key}+)`);
    if (reg.test(format)) {
      //获取匹配组的内容
      let content = RegExp.$1;
      let k =  o[key] >= 10 ? o[key] : content.length == 2 ? '0' + o[key] : o[key];
      format = format.replace(content, k);
    }
  }
  return format;
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
